import { COUNT_SOURSES } from '../../constants';
import './sources.css';
var Sources = /** @class */ (function () {
    function Sources() {
    }
    Sources.prototype.draw = function (data, page) {
        var fragment = document.createDocumentFragment();
        var sourceItemTemp = document.querySelector('#sourceItemTemp');
        data.slice(0, page * COUNT_SOURSES).forEach(function (item) {
            var sourceClone = sourceItemTemp.content.cloneNode(true);
            sourceClone.querySelector('.source__item-name').textContent = item.name;
            sourceClone.querySelector('.source__item').setAttribute('data-source-id', item.id);
            fragment.append(sourceClone);
        });
        document.querySelector('.sources').innerHTML = '';
        document.querySelector('.sources').append(fragment);
    };
    Sources.prototype.drawShowMore = function (callback) {
        var fragment = document.createElement('button');
        var main = document.querySelector('main');
        var div = document.createElement('div');
        fragment.innerHTML = 'more sources';
        fragment.classList.add('more');
        div.append(fragment);
        main.prepend(div);
        fragment.addEventListener('click', callback);
    };
    Sources.prototype.drawCategories = function (data, drawCategories) {
        var ulMain = document.createElement('ul');
        ulMain.classList.add('source-main');
        var aside = document.querySelector('aside');
        var sources = document.querySelector('.sources');
        sources.appendChild(aside);
        aside.appendChild(ulMain);
        data.forEach(function (item) {
            var liMain = document.createElement('li');
            liMain.id = item;
            liMain.innerHTML = item;
            ulMain.appendChild(liMain);
            liMain.addEventListener('click', function () {
                drawCategories(item);
            });
        });
    };
    Sources.prototype.drawCurrentCategories = function (data) {
        document.querySelector('.all').classList.remove('rotate-btn');
        if (document.querySelector('.source-current')) {
            document.querySelector('.all').classList.remove('rotate-btn');
            document.querySelector('.source-current').remove();
        }
        var ulCurrent = document.createElement('ul');
        ulCurrent.classList.add('source-current');
        var div = document.querySelector(".sources");
        div.appendChild(ulCurrent);
        data.forEach(function (item) {
            var liCurrent = document.createElement('li');
            liCurrent.id = item.name;
            liCurrent.classList.add('source__item');
            liCurrent.setAttribute('data-source-id', item.id);
            liCurrent.innerHTML = item.name;
            ulCurrent.appendChild(liCurrent);
        });
        var allSourcesBtn = document.querySelector('.all');
        allSourcesBtn.classList.add('display');
        allSourcesBtn.addEventListener('click', function () {
            ulCurrent.classList.toggle('height');
            allSourcesBtn.classList.toggle('rotate-btn');
        });
    };
    return Sources;
}());
export default Sources;
